
*{
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  font-family: sans-serif;
  overflow: hidden; 
}
.message{
  width: 80vw;
  margin-left: 10%;
  padding:2%;
}
.addContent{
  background-color: #68C4CA;
  color: white;

}
.addUsers{
  background-color: #84CA9D;
  color: white;

}
.formations,.help{
  background-color: #E5E5E5;
  color: black;
}

.dashboard{
  display: inline-grid;
  margin-left: 15%;

.card {
  width: 70vw;
  height: 15vh;
  border-radius: 6px;
  margin-bottom: 10%;
  display: flex;
  align-items: baseline;
}
.card>*{
  margin: auto 0;
  margin-left:10%;
}
.icon{
  width: 10%;
}
.text{
  font-size: 16px;
  width: 80%;
  padding: 2%;
  text-align: center;
  margin: auto auto;
}
}

@media screen and (min-width:400px){
  .text{
    font-size: 18px;
  }
  .message{
    font-size: 14px;
  }
}

@media screen and (min-width:500px){
  .text{
    font-size: 20px;
  }
  .message{
    font-size: 16px;
  }
}
